<template>
  <div class="single">
    <div class="home_content">
      <div class="head">
        <div class="buttom">
          <a href="/resource/template.xlsx" target=".xlsx"></a>
        </div>
        <input
          class="input-file"
          type="file"
          @change="exportData"
          accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
        />
        <div class="buttom" @click="importExcel"></div>
      </div>
      <div class="content">
        <SingleForm :list="allExcelData"></SingleForm>
      </div>
      <div class="footer">
        <div class="buttom" @click="ExportExcel"></div>
      </div>
    </div>
  </div>
</template>

<script>
import XLSX from "xlsx";
import SingleForm from "@/components/SingleForm.vue";
export default {
  name: "single",
  components: {
    SingleForm
  },
  data() {
    return {
      allExcelData: [], //excel
      fileName: "" //
    };
  },
  methods: {
    //
    importExcel() {
      document.querySelector(".input-file").click();
      console.log("");
    },
    exportData(event) {
      if (!event.currentTarget.files.length) {
        return;
      }
      const that = this;
      that.allExcelData = [];
      // 
      let f = event.currentTarget.files[0];
      this.fileName = f.name;
      console.log("", this.fileName);
      //excelfile$emit
      that.$emit("getMyExcelData", f);
      // FileReader
      let reader = new FileReader();
      // FileReaderreadAsBinaryString
      FileReader.prototype.readAsBinaryString = function(f) {
        let binary = "";
        let wb; // 
        let outdata; // 
        let reader = new FileReader();
        reader.onload = function() {
          // Uint8ArrayUnicodeUnicode
          let bytes = new Uint8Array(reader.result);
          let length = bytes.byteLength;
          for (let i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i]);
          }
          // xlsx
          wb = XLSX.read(binary, {
            type: "binary",
            cellDates: true //
          });
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]);
          //
          console.log("outdata", outdata);
          that.allExcelData = that.handleData(outdata);
        };
        reader.readAsArrayBuffer(f);
      };
      reader.readAsBinaryString(f);
    },
    handleData(data) {
      for (let item in data) {
        for (let key in data[item]) {
          if (key == "") {
            data[item]["type"] = data[item][key];
          } else if (key == "") {
            data[item]["date"] = data[item][key];
          } else if (key == "") {
            data[item]["name"] = data[item][key];
          } else if (key == "") {
            data[item]["address"] = data[item][key];
          }
        }
      }
      return data;
    },

    //
    ExportExcel() {
      console.log("");
      const { columns } = this;
      require.ensure([], () => {
        const { export_json_to_excel } = require("@/vendor/Export2Excel");
        //  ---require   Export2Excel.js
        const tHeader = ["", "", "", ""];
        //  ----tHeader 
        const filterVal = ["type", "name", "date", "address"];
        //filterVal  
        columns &&
          columns.map(data => {
            tHeader.push(data.value);
            filterVal.push(data.key);
          });
        const list = this.allExcelData;
        // json
        const data = this.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, "");
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    }
  }
};
</script>
<style lang="scss" scoped>
// $head:70px;
.buttom {
  cursor: pointer;
  width: 120px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border-radius: 4px;
  margin: 0 20px;
  background-color: #fefefe;
  border: 1px solid #eeeeee;
  transition: all 1s linear;
  a {
    color: #333;
    text-decoration: none;
  }
}
.buttom:hover {
  background-color: rgba($color: #409eff, $alpha: 0.8);
  color: #f1f1f1;
  a:hover {
    color: #f1f1f1;
  }
}
.single {
  width: 100%;
  height: 100%;
  .home_content {
    height: 100%;
    .head {
      height: 40px;
      padding: 10px 0;
      display: flex;
      justify-content: flex-end;
      .input-file {
        display: none;
      }
    }

    .content {
      height: calc(100% - 140px);
    }

    .footer {
      height: 40px;
      margin: 20px 0;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
</style>
